html {
  background-color: #c9c9c9;
}

.App {
  text-align: center;
}

.Container {
  margin: auto;
  width: 80%;
}

.Container-left {
  width: 50%;
}

.Container-right {
  width: 50%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Navbar {
  margin-top: 3%;
  margin: auto;
  width: 100%;
  height: 80px;
  position: relative;
  background-color: #181A1B;
}

.Navbar-logo {
  vertical-align: middle;
  height: 70px;
  cursor: pointer;
}

.Navbar-navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.Navbar-item {
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  padding: 1rem;
}

.Navbar-item:hover,
.Navbar-item:active {
  color: white;
  cursor: pointer;
}

.Navbar-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.Navbar-spacer {
  flex: 1;
}

.Toggle-Button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.Toggle-Button:focus {
  outline: none;
}

.Toggle-Button-line {
  width: 40px;
  height: 3px;
  background: gainsboro;
}

.Backdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
}

.Side-Drawer {
  height: 100vh;
  background-color: #181A1B;
  box-shadow: 2px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.Side-Drawer.open {
  transform: translateX(0);
}

.Side-Drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Side-Drawer-item {
  text-align: left;
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: calc(15px + 1vmin);
  padding: 1rem;
}

.Side-Drawer-item:hover, 
.Side-Drawer-item:active {
  color: white;
}

.Side-Drawer li {
  margin: 0.5rem 0;
}

@media (max-width: 700px) {
  .Navbar-items {
    display: none;
  }
}

@media (min-width: 700px) {
  .Toggle-Button {
    display: none;
  }
}

.Line-separator-full {
  height: 2px;
  background-color: black;
  width: 100%;
}

.Loading-animation {
  margin-top: 10%;
  width: 10vw;
}

.Choice-button {
  width: 400px;
  border: solid #2196F3 2px;
  border-radius: 10%;
}

.Choice-button:hover {
  background-color: #2196F3;
  border: solid black 2px;
}

.Heading {
  margin-top: 5%;
  color: #2196F3;
}

.Topic-item {
  margin-top: 3%;
}
